"use client";

import { useState } from "react";
import { Eye, EyeOff } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";

export default function AuthPage() {
  const [isLogin, setIsLogin] = useState(true);
  const [showPassword, setShowPassword] = useState(false);

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-[#DCE9FB] px-4">
      <Card className="w-full max-w-sm bg-white rounded-2xl shadow-md p-6">
        {/* Titlu */}
        <CardHeader className="text-center mb-4">
          <CardTitle className="text-2xl font-semibold bg-gradient-to-b from-[#0C3057] to-[#31649A] bg-clip-text text-transparent">
            {isLogin ? "Welcome to Skill Trade" : "Create your account"}
          </CardTitle>
          {isLogin && (
            <p className="text-sm text-gray-700 mt-1">
              Login or Sign up to access your account
            </p>
          )}
        </CardHeader>

        {/* Toggle Login / Sign Up */}
        <div className="flex mb-4 border border-gray-200 rounded-lg overflow-hidden">
          <button
            onClick={() => setIsLogin(true)}
            className={`flex-1 py-2 font-semibold ${
              isLogin ? "bg-[#DCE9FB]" : "bg-white"
            }`}
          >
            Login
          </button>
          <button
            onClick={() => setIsLogin(false)}
            className={`flex-1 py-2 font-semibold ${
              !isLogin ? "bg-[#DCE9FB]" : "bg-white"
            }`}
          >
            Sign Up
          </button>
        </div>

        {/* Formular Login */}
        {isLogin ? (
          <CardContent className="space-y-4">
            <div>
              <Input
                type="email"
                placeholder="Email Address"
                className="bg-white border-gray-300"
              />
            </div>

            <div className="relative">
              <Input
                type={showPassword ? "text" : "password"}
                placeholder="Password"
                className="bg-white border-gray-300"
              />
              <div
                className="absolute right-3 top-3 cursor-pointer"
                onClick={() => setShowPassword(!showPassword)}
              >
                {showPassword ? (
                  <EyeOff className="w-5 h-5 text-gray-500" />
                ) : (
                  <Eye className="w-5 h-5 text-gray-500" />
                )}
              </div>
            </div>

            <div className="text-right">
              <a
                href="#"
                className="text-sm font-semibold text-[#6085B9] hover:underline"
              >
                Forgot password?
              </a>
            </div>

            <Button className="w-full bg-[#6085B9] hover:bg-[#4f73a4] text-white font-semibold">
              Login
            </Button>
          </CardContent>
        ) : (
          /* Formular Sign Up */
          <CardContent className="space-y-4">
            <div>
              <Input
                type="text"
                placeholder="Name"
                className="bg-white border-gray-300"
              />
            </div>

            <div>
              <Input
                type="email"
                placeholder="Email Address"
                className="bg-white border-gray-300"
              />
            </div>

            <div>
              <Input
                type="password"
                placeholder="Password"
                className="bg-white border-gray-300"
              />
            </div>

            <div>
              <Input
                type="password"
                placeholder="Confirm Password"
                className="bg-white border-gray-300"
              />
            </div>

            <Button className="w-full bg-[#6085B9] hover:bg-[#4f73a4] text-white font-semibold">
              Sign Up
            </Button>

            <p className="text-center text-sm text-gray-700">
              Have an account?{" "}
              <span
                onClick={() => setIsLogin(true)}
                className="font-semibold text-[#6085B9] cursor-pointer hover:underline"
              >
                SIGN IN
              </span>
            </p>
          </CardContent>
        )}
      </Card>
    </div>
  );
}
